const HistoricalDataRules = ({ formik }) => {
  const { errors, values, touched, getFieldProps } = formik;
  return (
    <FieldArray
      name="historicalDataRules"
      render={(historicalDataArrayHelpers) => (
        <Card>
          <CardHeader
            title="Historical Data Rules"
            titleTypographyProps={{ variant: 'subtitle1' }}
            action={
              <Button
                size="small"
                variant="contained"
                startIcon={<Icon icon={PlusFill} />}
                onClick={() => historicalDataArrayHelpers.push({ ...initialFromToObject })}
              >
                Add Rule
              </Button>
            }
          />
          <CardContent>
            <Stack spacing={2}>
              {values.historicalDataRules.map((rule, index) => {
                const fieldNamePrefix = `historicalDataRules.${index}`;
                return (
                  <Grid container key={index} justifyContent="space-between">
                    <Grid item sm={12} md={6}>
                      <Grid container spacing={1}>
                        <Grid item sm={12} md={2}>
                          <Typography>did not win</Typography>
                        </Grid>
                        <Grid item sm={12} md={4}>
                          <Stack direction="row" spacing={1}>
                            <TextField
                              type="number"
                              fullWidth
                              variant="standard"
                              size="small"
                              {...getFieldProps(`${fieldNamePrefix}.from`)}
                              error={Boolean(
                                getIn(touched, `${fieldNamePrefix}.from`) && getIn(errors, `${fieldNamePrefix}.from`)
                              )}
                              helperText={
                                getIn(touched, `${fieldNamePrefix}.from`) && getIn(errors, `${fieldNamePrefix}.from`)
                              }
                            />
                            <Typography>to</Typography>
                          </Stack>
                        </Grid>
                        <Grid item sm={12} md={4}>
                          <Stack direction="row" spacing={1}>
                            <TextField
                              type="number"
                              fullWidth
                              variant="standard"
                              size="small"
                              {...getFieldProps(`${fieldNamePrefix}.to`)}
                              error={Boolean(
                                getIn(touched, `${fieldNamePrefix}.to`) && getIn(errors, `${fieldNamePrefix}.to`)
                              )}
                              helperText={
                                getIn(touched, `${fieldNamePrefix}.to`) && getIn(errors, `${fieldNamePrefix}.to`)
                              }
                            />
                            <Typography>times</Typography>
                          </Stack>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item sm={12} md={4}>
                      <Stack direction="row">
                        <TextField
                          type="number"
                          fullWidth
                          variant="standard"
                          size="small"
                          {...getFieldProps(`${fieldNamePrefix}.tickets`)}
                          error={Boolean(
                            getIn(touched, `${fieldNamePrefix}.tickets`) && getIn(errors, `${fieldNamePrefix}.tickets`)
                          )}
                          helperText={
                            getIn(touched, `${fieldNamePrefix}.tickets`) && getIn(errors, `${fieldNamePrefix}.tickets`)
                          }
                        />
                        <Typography>tickets</Typography>
                        <IconButton
                          color="error"
                          aria-label="delete"
                          onClick={() => historicalDataArrayHelpers.remove(index)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </Stack>
                    </Grid>
                  </Grid>
                );
              })}
            </Stack>
          </CardContent>
        </Card>
      )}
    />
  );
};

