const NewTicketRuleSchema = Yup.object().shape({
  name: Yup.string().required('Name is required'),
  type: Yup.string(),
  historicalDataRules: Yup.array(),
  appLevelRules: Yup.array(),
  followerRules: Yup.array(),
  socialNetworks: Yup.array(),
  socialNetworkRules: Yup.array(),
});

const TicketRuleNewForm = ({ isEdit, currentTicketRule }) => {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: currentTicketRule?.name || '',
      types: currentTicketRule?.types || ['Historical Data', 'App Level', 'Followers', 'Social'],
      historicalDataRules: currentTicketRule?.historicalDataRules || [{ ...initialFromToObject }],
      appLevelRules: currentTicketRule?.appLevelRules || [{ ...initialFromToObject }],
      followerRules: currentTicketRule?.followerRules || [{ ...initialFromToObject }],
      socialNetworks: currentTicketRule?.socialNetworks || [],
      socialNetworkRules: currentTicketRule?.socialNetworkRules || [{ ...initialSocialRuleObject }],
    },
    validationSchema: NewTicketRuleSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.bonus.createTicketRule);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, getFieldProps, setFieldValue } = formik;

  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Card>
              <CardContent>
                <Stack spacing={3}>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      fullWidth
                      size="small"
                      label="Name"
                      {...getFieldProps('name')}
                      error={Boolean(touched.name && errors.name)}
                      helperText={touched.name && errors.name}
                    />
                    <Autocomplete
                      fullWidth
                      multiple
                      disableCloseOnSelect
                      size="small"
                      value={values.types}
                      onChange={(event, newValue) => {
                        setFieldValue('types', newValue);
                      }}
                      options={['Historical Data', 'App Level', 'Followers', 'Social']}
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          label="Ticket Rules"
                          error={Boolean(touched.types && errors.types)}
                          helperText={touched.types && errors.types}
                        />
                      )}
                    />
                  </Stack>

                  {values.types.includes('Historical Data') && <HistoricalDataRules formik={formik} />}
                  {values.types.includes('App Level') && <AppLevelRules formik={formik} />}
                  {values.types.includes('Followers') && <FollowerRules formik={formik} />}
                  {values.types.includes('Social') && <SocialNetworkRules formik={formik} />}

                  <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                      Save
                    </LoadingButton>
                  </Box>
                </Stack>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
};

HistoricalDataRules.propTypes = {
  formik: PropTypes.object,
};
AppLevelRules.propTypes = {
  formik: PropTypes.object,
};
FollowerRules.propTypes = {
  formik: PropTypes.object,
};
SocialNetworkRules.propTypes = {
  formik: PropTypes.object,
};

TicketRuleNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentTicketRule: PropTypes.object,
};

export default TicketRuleNewForm;
