import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import TicketRuleNewForm from '../components/bonus/TicketRuleNewForm';

// ----------------------------------------------------------------------

export default function TicketRuleCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with Ticket Rule
  const currentTicketRule = {};

  return (
    <Page title="TicketRule: Create a new bonus">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Ticket Rule' : 'Edit Ticket Rule'}
          links={[{ name: 'TicketRule', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add Ticket Rule' : name }]}
        />
        <TicketRuleNewForm isEdit={isEdit} currentTicketRule={currentTicketRule} />
      </Container>
    </Page>
  );
}
